.burger_wrapper {
  display: none;
  position: relative;
  margin-right: 1.5em;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  height: 2em;
  font-size: 1.5em;
  position: absolute;
  right: 3px;
  z-index: 100;
  top: 50%;
  transform: translateY(-50%);
}
@media (max-width: 1040px) {
  .burger_wrapper {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
  }
}
.burger_wrapper .burger {
  display: inline-block;
  width: 1.5em;
  height: 2px;
  background-color: #1D52A5;
  position: relative;
  transition: .3s;
}
.burger_wrapper .burger:after,
.burger_wrapper .burger:before {
  content: '';
  display: inline-block;
  width: 1.5em;
  height: 2px;
  background-color: #1D52A5;
  position: absolute;
  left: 0;
  transition: .3s;
}
.burger_wrapper .burger:after {
  top: -0.5em;
}
.burger_wrapper .burger:before {
  bottom: -0.5em;
}
.burger_wrapper .burger.active {
  height: 0;
}
.burger_wrapper .burger.active:after {
  top: -0.1em;
  transform: rotate(45deg);
}
.burger_wrapper .burger.active:before {
  bottom: 0;
  transform: rotate(-45deg);
}
